import React from 'react'

function Ending({ background: BackgroundImage }) {
    const background = {
        backgroundImage: `url(${BackgroundImage})`
    }
    const redirect = () => {
        window.location = "https://www.messenger.com/t/100006006275457";
    }

    return (
        <div className="container ending" style={background}>
            <button onClick={redirect}>Continue?</button>
        </div>
    )
}

export default Ending
